.header-section__navbar-desktop {
    width: 332px;
    height: 32px;
    display: flex;
    align-items: center;

    .navbar-list__desktop {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0;
        list-style: none;

        &__link {
            font-family: Inter, sans-serif;
            font-weight: 400;
            font-size: 18px;
            line-height: 21.78px;
            text-align: center;
            color: #000;
            text-decoration: none;
        }
    }
}